<template>
  <div class="product-list">
    <div class="wrapper">
      <Item 
        v-for="(item, i) in datas" 
        :key="i" 
        :model="item"
        :isSelected="i == tabIndex"
        @click.native="changeTabIndex(i)" />
    </div>
  </div>
</template>

<script>
  import Item from './Item'
  export default {
    components:{
      Item
    },
    props:{},
    data(){
      return {
        datas: [
          {
            days: '7天',
            hot: false,
            rate: '2.87%~3.05%'
          },
          {
            days: '14天',
            hot: false,
            rate: '3.87%~4.05%'
          },
          {
            days: '30天',
            hot: true,
            rate: '4.87%~5.05%'
          },
          {
            days: '60天',
            hot: false,
            rate: '5.87%~6.05%'
          }
        ],
        tabIndex: 0
      }
    },
    watch:{},
    computed:{},
    methods:{ 
      changeTabIndex(_tabIndex) {
        if(this.tabIndex == _tabIndex) {
          return;
        }
        this.tabIndex = _tabIndex;
        this.$emit('changeTabIndex', _tabIndex);
      }
    },
    created(){},
    mounted(){}
  }
</script>

<style lang="less" scoped>
  .product-list {
    margin-top: 4vw;
    padding: 4vw;
    .wrapper {
      display: flex;
      overflow-x: scroll;
    }
    
  }
</style>