<template>
  <div class="money-input">
    <div class="title">{{ title }}（元）</div>
    <div class="input-group">
      <div>¥</div>
      <div class="split"></div>
      <div class="input">
        <input v-model="money" type="number" />
        <div 
          class="place-holder"
          v-show="money.length == 0">{{ hintText }}</div>
      </div>
      <div class="del-tag" v-show="money.length > 0">
        <img @click="clearInput()" src="./img_del.png" />
      </div>
    </div>
    <div class="tips">{{ inputTips }}</div>
  </div>
</template>

<script>
  export default {
    components:{},
    props:{
      title: String,
      maxValue: Number,
      inputTips: String,
      hintText: String
    },
    data(){
      return {
        money: ''
      }
    },
    watch:{
      money(val) {
        this.$emit('valueChange', val);
      }
    },
    computed:{},
    methods:{
      clearInput() {
        this.money = '';
      }
    },
    created(){},
    mounted(){}
  }
</script>

