<template>
  <div class="rollin">
    <Header 
      title="转入" 
      :hasBottomBorder="true"/>
    <div class="wrapper">
      <div class="txt-normal txt-center balance-tit">可用钱包余额</div>
      <div class="balance txt-center">¥8000.00</div>
      <MoneyInput 
        v-on:valueChange="valueChange" 
        :maxValue="8000"
        :inputTips="inputTips"
        title="转入金额"
        hintText="请输入转入金额" />
      <div class="tips txt-normal">
        2019.12.09-2020.03.08 收益每日返还，到期本金返还
      </div>
      <div class="rollin">
          <a 
            :class="buttonStyle"
            @click="doRollIn()">确认转入</a>
      </div>
    </div>
  </div>
</template>

<script>
  import Header from '../../components/Header'
  import MoneyInput from '../../components/MoneyInput'
  export default {
    components:{
      Header,
      MoneyInput
    },
    props:{
    },
    data(){
      return {
        inputValue: 0,
        buttonStyle: 'btn btn-disable',
        inputTips: ''
      }
    },
    watch:{},
    computed:{},
    methods:{
      valueChange(value) {
        this.inputValue = value;
        if(value == '') {
          this.inputTips = ''; 
          this.buttonStyle = 'btn btn-disable'
        } else if(!isNaN(value)) {
          this.checkInputValue();
        } else {
          this.buttonStyle = 'btn btn-disable'
          this.inputTips = '请输入正确的金额';
        }
      },
      //TODO 这里需要使用正则表达式校验
      checkInputValue() {
        //TODO 需要根据业务更换具体数值
        if(this.inputValue > 8000) {
          this.buttonStyle = 'btn btn-disable'
          this.inputTips = '您输入的值大于可用钱包余额';
        } else if(this.inputValue <= 0) {
          this.buttonStyle = 'btn btn-disable'
          this.inputTips = '请输入正确的金额';
        } else {
          this.buttonStyle = 'btn active'
          this.inputTips = '';
        }
      },
      doRollIn() {
        //TODO 输入校验
        if(this.inputValue.length > 0
          && this.inputValue <= 8000 
          && !isNaN(this.inputValue)) {
          this.$router.replace(`rollsuccess/0/${this.inputValue}`)
        }
      }
    },
    created(){},
    mounted(){}
  }
</script>

