const MENU_NAMES = ['收益列表', '转入列表', '转出列表'];
const TIPS = ['收益', '转入', '转出'];
// const ROUTERS = ['/earnings', ]

const state = {
  listType: 0,
  isShowed: false
};

const getters = {
  menuName: (state) => {
    return MENU_NAMES[state.listType]
  },
  tipsText: (state) => {
    return TIPS[state.listType]
  }
};

const actions = {
  changeListType( {commit}, listType ) {
    commit('changeListType', listType);
  },
  setMenuVisible( {commit}, isShow ) {
    commit('setMenuVisible', isShow);
  }
};

const mutations = {
  changeListType(state, listType) {
    state.listType = listType;
  },
  setMenuVisible(state, isShow) {
    state.isShowed = isShow;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}