<template>
  <div class="small">
    <line-chart 
    :chartData="datacollection"
    :options="options"
    ></line-chart>
    <button @click="fillData()">Randomize</button>
  </div>
</template>

<script>
  import LineChart from './LineChart.js'

  export default {
    components: {
      LineChart
    },
    data () {
      return {
        datacollection: null,
        options: {
          scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true
                }
            }],
            gridLines: {
              display: false,
              //circular: true
              drawBorder: false
            }
          },
          title: {
            display: false
          },
          pointLabels: {
            // Boolean - if true, show point labels
            display: false,

            // Number - Point label font size in pixels
            fontSize: 10,

            // Function - Used to convert point labels
            callback: function(label) {
              return label;
            }
          },
          scaleLabel: {
            // display property
            display: false
          }
        }
      }
    },
    mounted () {
      this.fillData()
    },
    methods: {
      fillData () {
        this.datacollection = {
          labels: ['11-05', '11-13', '11-20'],
          datasets: [
            {
              label: '',
              backgroundColor: 'rgba(99, 162, 248, 0.2)',
              borderColor	: 'rgba(99, 162, 248, 1)',
              borderWidth: 4,
              data: [2.007, 4.152,  5.168],
              pointBorderWidth: 0,
              pointHitRadius: 0
            }
          ]
        }
      },
      getRandomInt () {
        return Math.floor(Math.random() * (50 - 5 + 1)) + 5
      }
    }
  }
</script>

