<template>
  <div class="failed">
    <Header 
      :showModel="1" 
      :hasBottomBorder="true" />
    <img src="./img_failed.png" />
    <div class="success-text">{{ this.rollType }}</div>
  </div>
</template>

<script>
  import Header from '../Header'
  export default {
    components:{
      Header
    },
    props:{
    },
    data(){
      return {
        rollType: '转入失败',
        rollMoney: '0'
      }
    },
    watch:{
      '$route'() {
        this.rollType = this.$route.params.type == 0 ? '转入失败' : '转出失败';
        this.rollMoney = this.$route.params.money;
      }
    },
    computed:{},
    methods:{},
    created(){
      this.rollType = this.$route.params.type == 0 ? '转入失败' : '转出失败';
      this.rollMoney = this.$route.params.money;
    },
    mounted(){}
  }
</script>

