import Index from './views/index';
import Earnings from './views/earnings';
import EarningsRate from './views/earningsrate';
import Rollin from './views/rollin';
import Rollout from './views/rollout';
import RollinHighProduct from './views/rollinhighproduct';
import RandomChart from './views/test/RandomChart';
import { RollSuccess, RollFailed } from './components/RollResult/index.js';
import HighEarnings from './views/highearnings';
import OwnProducts from './views/ownproducts';
import ProductHistory from './views/historyproducts';
import VueRouter from 'vue-router';
import Vue from 'vue';
import store from './store';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: Index
  },
  {
    path: '/earnings',
    component: Earnings
  },
  {
    path: '/earningsrate',
    component: EarningsRate
  },
  {
    path: '/rollin',
    component: Rollin
  },
  {
    path: '/rollout',
    component: Rollout
  },
  {
    path: '/rollsuccess/:type?/:money?',
    component: RollSuccess
  },
  {
    path: '/rollfailed/:type/:money',
    component: RollFailed
  },
  {
    path: '/highearnings',
    component: HighEarnings
  },
  {
    path: '/myproducts',
    component: OwnProducts
  },
  {
    path: '/producthistory',
    component: ProductHistory
  },
  {
    path: '/rollinhighproduct',
    component: RollinHighProduct
  },
  {
    path: '/test',
    component: RandomChart
  }
];

const router = new VueRouter({
  linkActiveClass: 'active',
  routes
});

export { router, store};