<template>
  <div class="rollout">
    <Header 
      title="转出" 
      :hasBottomBorder="true"/>
    <div class="wrapper">
      <div class="txt-normal txt-center balance-tit">可转出钱包余额</div>
      <div class="balance txt-center">¥8060.00</div>
      <MoneyInput 
        v-on:valueChange="valueChange" 
        :maxValue="8060"
        :inputTips="inputTips"
        title="转出金额"
        hintText="请输入转出金额" />
      <div class="rollout">
          <a 
            :class="buttonStyle"
            @click="doRollOut()">确认转出</a>
      </div>
    </div>
  </div>
</template>

<script>
  import Header from '../../components/Header'
  import MoneyInput from '../../components/MoneyInput'
  export default {
    components:{
      Header,
      MoneyInput
    },
    props:{
    },
    data(){
      return {
        inputValue: 0,
        buttonStyle: 'btn btn-disable',
        inputTips: ''
      }
    },
    watch:{},
    computed:{},
    methods:{
      valueChange(value) {
        this.inputValue = value;
        if(value == '') {
          this.inputTips = '';
          this.buttonStyle = 'btn btn-disable'
        } else if(!isNaN(value)) {
          this.checkInputValue();
        } else {
          this.buttonStyle = 'btn btn-disable'
        }
      },
      checkInputValue() {
        //TODO 需要根据业务更换具体数值

        if(this.inputValue > 8060) {
          this.buttonStyle = 'btn btn-disable'
          this.inputTips = '您输入的值大于可提出余额';
        } else if(this.inputValue <= 0) {
          this.inputTips = '请输入正确的金额';
        } else {
          this.buttonStyle = 'btn active'
          this.inputTips = '';
        }
      },
      doRollOut() {
        //TODO 输入校验
        if(this.inputValue.length > 0
          && this.inputValue <= 8060 
          && !isNaN(this.inputValue)) {
          this.$router.replace(`rollsuccess/1/${this.inputValue}`)
        }
      }
    },
    created(){},
    mounted(){}
  }
</script>

